import React, { Component } from 'react';
import {PageHeader} from 'react-bootstrap';
import { Card, Button, CardImg, CardTitle, CardText, CardDeck,
 CardSubtitle, CardBody ,CardLink} from 'reactstrap';
import axios from 'axios';
import './App.css';
import { Link } from 'react-router-dom';
import {Row,Col,Grid} from 'react-bootstrap';
import {Timeline, TimelineEvent} from 'react-event-timeline';
import {Modal,Table,tr,thead,tbody,td} from 'react-bootstrap';
class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      notes:[],
      timelines:[],
      show:false,
      modalTitle : "",
      best_moment:"",
      family_message:"",
      health_message:"",
      future_message:"",
      tmrw_message:""
    }
    this.groupBy = this.groupBy.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleShow = this.handleShow.bind(this);
    this.sortDates = this.sortDates.bind(this);
  }
  componentDidMount(){
    axios('/notes').then(res => {
      var results = this.groupBy("journal_date",res.data);
      results = this.sortDates(results);
      this.setState({
        notes : res.data,
        timelines:results
       });
      //debugger;
      console.log(this.state.notes);
    });
  }
  handleClose() {
    //debugger;
    this.setState({
      show: false,
      modalTitle:"",
      best_moment:"",
      family_message:"",
      health_message:"",
      future_message:"",
      tmrw_message:""
     });
  }

  handleShow(note) {
    this.setState({
      show: true,
      modalTitle:note.title,
      best_moment:note.best_moment,
      family_message:note.family_message,
      health_message:note.health_message,
      future_message:note.future_message,
      tmrw_message:note.tmrw_message
    })
  }
  sortDates(arr){
    arr.sort(function(obj1,obj2){
      let aDate = new Date(obj1.journal_date);
      let bDate = new Date(obj2.journal_date);
      return bDate - aDate;
    });
    return arr;
  }
 groupBy(key, array) {
  var result = [];
  for (var i = 0; i < array.length; i++) {
    var added = false;
    for (var j = 0; j < result.length; j++) {
      if (result[j][key] == array[i][key]) {
        result[j].items.push(array[i]);
        added = true;
        break;
      }
    }
    if (!added) {
      var entry = {items: []};
      entry[key] = array[i][key];
      entry.items.push(array[i]);
      result.push(entry);
    }
  }
  return result;
}
  render() {
    return (
      <div className="container app">
          <div className="page-header-row">
            <h2 className="page-header">Journal 308
            <img url="./us.jpg"></img>
            </h2>

          </div>
          <Row className="action-button-row">
            <Link className="add-entry-sh-btn" to={{ pathname: '/create Shivani', state: { className: 'note_shivani'} }}><span className="glyphicon glyphicon-plus-sign" aria-hidden="true"></span>New Entry - Shivani</Link>
          <Link className="add-entry-so-btn" to={{ pathname: '/create Sourabh', state: { className: 'note_sourabh'} }}><span className="glyphicon glyphicon-plus-sign" aria-hidden="true"></span>New Entry - Sourabh</Link>
            <hr className="brace"></hr>
          </Row>
          <Row>
            <Timeline className="timeline">
              {this.state.timelines.map((timeline,index)=>
                <TimelineEvent
                  key={index}
                  title={timeline.journal_date}
                  icon={<i />}
                  iconColor="#6fba1c"
                  className="timeline-event" >
                {timeline.items.map((note,itr)=>
                  <Col xs={12} md={6} lg={3} className="timeline-event-col" key={itr}>
                    <Card className={note.className} onClick={this.handleShow.bind(this,note)}>
                      <CardBody>
                        <div dangerouslySetInnerHTML={{__html: note.title}}></div>
                      </CardBody>
                      <CardBody>
                        <Row>
                          <div>

                          </div>
                        </Row>
                      </CardBody>
                    </Card>
                  </Col>

                )}

              </TimelineEvent>
              )}
              </Timeline>
          </Row>
          <Row>
            <div>
              <Modal show={this.state.show} className="" onHide={this.handleClose.bind(this)}>
                  <Modal.Header closeButton className="modal-title">
                    <Modal.Title>
                      {
                         <div  dangerouslySetInnerHTML={{__html: this.state.modalTitle}}></div>
                      }
                    </Modal.Title>
                  </Modal.Header>
                  <Modal.Body className="container-fluid modal-desc">
                    <Row>
                      <Col xs={12} lg={12}>
                      <Card className="best-moment-pop">
                        <CardBody>
                          <CardTitle className="card-title-pop">Best moment of the day</CardTitle>
                        </CardBody>
                        <CardBody className="best-moment-body-pop">
                            <div>
                               <div dangerouslySetInnerHTML={{__html: this.state.best_moment}}></div>
                            </div>
                        </CardBody>
                      </Card>
                    </Col>
                      <Col xs={12} lg={12}>
                        <Card className="health-pop">
                          <CardBody>
                            <CardTitle className="card-title-pop">What did I do today to make my health better?</CardTitle>
                          </CardBody>
                          <CardBody className="health-body-pop">
                              <div>
                                 <div dangerouslySetInnerHTML={{__html: this.state.health_message}}></div>
                              </div>
                          </CardBody>
                        </Card>
                      </Col>
                      <Col xs={12} lg={12}>
                        <Card className="family-pop">
                          <CardBody>
                            <CardTitle className="card-title-pop">Throwback to one memory with Sourabh/Shivani</CardTitle>
                          </CardBody>
                          <CardBody className="family-body-pop">
                              <div>
                                 <div dangerouslySetInnerHTML={{__html: this.state.family_message}}></div>
                              </div>
                          </CardBody>
                        </Card>
                      </Col>
                      <Col xs={12} lg={12}>
                        <Card className="future-pop">
                          <CardBody>
                            <CardTitle className="card-title-pop">What did I do today to make my future better?</CardTitle>
                          </CardBody>
                          <CardBody className="future-body-pop">
                              <div>
                                 <div dangerouslySetInnerHTML={{__html: this.state.future_message}}></div>
                              </div>
                          </CardBody>
                        </Card>
                      </Col>
                      <Col xs={12} lg={12}>
                        <Card className="tmrw-pop">
                          <CardBody>
                            <CardTitle className="card-title-pop">One thing I want to do better tomorrow?</CardTitle>
                          </CardBody>
                          <CardBody className="tmrw-body-pop">
                              <div>
                                 <div dangerouslySetInnerHTML={{__html: this.state.tmrw_message}}></div>
                              </div>
                          </CardBody>
                        </Card>
                      </Col>
                  </Row>
                  <Row>
                    <Button className="close-popover" color="danger" onClick={this.handleClose}>Close</Button>
                  </Row>
                  </Modal.Body>
              </Modal>
            </div>
          </Row>
        </div>
    );
  }
}
export default App;
